@font-face {
  font-family: 'Schadow';
  font-stretch: condensed;
  src: url(../src/Schadow/SchadowBlackCondensed.ttf);
}

.container, .row {
  height: 100%;
}

.container-body {
  min-height: 400px;
}

.col {
  margin: auto 0;
}

p {
  margin: 0;
}

p, .nav-link, table, body {
  font-family: 'Spectral', serif !important;
}

.cover {
  height: 450px;
  position: relative;
  top: -100px;
  box-shadow: 14px 14px 12px #000000;
}

.cover-books {
  height: 450px;
  margin: 20px;
  position: relative;
  box-shadow: 14px 14px 12px #000000;
}

.back {
  height: 470px;
  -moz-box-shadow: 14px 14px 12px #000000;
  -webkit-box-shadow: 14px 14px 12px #000000;
  box-shadow: 14px 14px 12px #000000;
}

.jumbotron {
  background-color: #173041 !important;
  color: #e8e8c9;
  margin-bottom: 5rem !important;
  font-family: "Schadow";
  height: 375px;
}

.jumbo-homepage {
  margin-bottom: 100px !important;
}

.jumbotron h1 {
  font-size: 60px;
  margin-left: 20px;
}

.jumbotron h2 {
  font-size: 50px;
}

.jumbotron_secondary {
  padding: 3rem 2rem !important;
  height: 225px;
}

.jumbotron_tertiary {
  padding: 3rem 2rem !important;
  height: 120px;
  margin-bottom: 3rem !important;
}

.jumbotron_tertiary h2 {
  top: -16px;
  position: relative;
}

.nav {
  font-size: 18px;
  font-weight: 700;
  margin-right: 5px;
  height: 70px;
}

.nav-home {
  margin-bottom: 50px;
}

.nav-item a {
  line-height: 55px;
}

.nav-link {
  color: #173040c2;
}

.nav-link:hover, .footer, .footer a {
  color: #e8e8c9 !important;
}

.table {
  font-size: 1.2rem;
}

.footer a {
  text-decoration: underline;
}

.big-W {
  font-size: 50px;
  margin-right: -5px;
  line-height: 28px;
}

p {
  line-height: 30px;
  text-align: left;
}

.img0 {
  position: absolute;
  width: 400px;
  height: auto;
  left: 0;
  -moz-box-shadow: 14px 14px 12px #000000;
  -webkit-box-shadow: 14px 14px 12px #000000;
  box-shadow: 14px 14px 12px #000000;
  border-radius: 5px;
}

.img1 {
  position: absolute;
  width: 400px;
  height: auto;
  right: 40px;
  bottom: 0;
  -moz-box-shadow: 14px 14px 12px #000000;
  -webkit-box-shadow: 14px 14px 12px #000000;
  box-shadow: 14px 14px 12px #000000;
  border-radius: 5px;
}

.footer {
  height: 225px;
  margin: 55px 0 0 0 !important;
  font-family: 'Spectral', serif;
  text-align: left;
  padding: 0 10% !important;
  position: absolute;
  width: 100%;
}

.footer h4 {
  margin-bottom: 20px;
}

.footer ul {
  padding: 0;
}

.footer li {
  list-style: none;
  margin-bottom: 10px;
}

input {
  width: 80%;
  border-radius: 5px;
  height: 40px;
  padding: 5px 15px;
  margin-right: 10px !important;
}

.fa-check-square {
  position: absolute;
  font-size: 40px !important;
  cursor: pointer;
}

hr {
  border: 0;
  border-top: 5px solid !important;
  color: #173040c2;
  margin-top: 0.3rem !important;
  margin-bottom: 0.3rem !important;
}

.hr1 {
  margin-bottom: 50px !important;
}

.author {
  color: #173041 !important;
  font-weight: bold;
  font-weight: 600 !important;
  margin-top: 15px;
  margin-bottom: 30px;
}

.profile {
  height: 400px;
  width: auto;
  border-radius: 5px;
}

img {
  border-radius: 3px;
}

.lessonButton {
  min-width: 130px;
}

/* Typography */

h3 {
  color: #565656 !important;
}

.caption {
  font-size: 20px;
}

/* Utility Classes */

.noMargin {
  margin: 0 !important;
}

.textCenter {
  text-align: center;
}

.textLeft {
  text-align: left;
}

.textRight {
  text-align: right;
}

.justifyCenter {
  justify-content: center;
}

.shortDownSpacer {
  margin-bottom: 50px;
}

.bloomsWheel {
  width: 650px;
  height: 650px;
  -webkit-animation: spin 2s linear infinite;
  -moz-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

.bloomsWheelStopped {
  height: auto;
  max-width: 650px;
}

.cImage {
  max-height: 300px;
  width: auto;
  margin: 0 auto;
}

.carousel {
  margin-top: 20px;
}

/* animations */

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}